.footer-nav {
  margin-bottom: 1.5rem;
  clear: both;
  &:last-child {
    margin-bottom: 0; }
  @include media-breakpoint-down(sm) {
    margin-bottom: .5rem; } }

.footer-nav__title {
  @include text-block(1rem 1.6 .5rem); }

.footer-nav__title-icon {
  margin-right: .5rem; }

.footer-nav__item {
  margin-bottom: 1rem; }

.tags-nav__item {
  @include inline-block(bottom); }

.tags-nav__link {
  margin-right: 1rem;
  @include text-block(.875rem 1.6, $text-color);
  @include hover-link;
  color: $text-color;
  font-weight: 400 !important;
  &:hover {
    color: $accent-color; }
  &.is-current {
    color: $accent-color; }
  @include media-breakpoint-down(sm) {
    margin-bottom: .5rem;
    display: block; } }



