.header {
  background-color: $white;
  @include padding(vertical, .75rem);
  @include media-breakpoint-down(sm) {
    @include padding(vertical, 0);
    background-color: transparent; } }

.header__inner {
  display: flex;
  height: 2rem;
  justify-content: flex-end;
  align-items: flex-end;
  @include position(relative);
  @include media-breakpoint-down(sm) {
    height: auto; } }

.header__icicle-image {
  @include position(absolute, left 0, top 2.625rem);
  display: block;
  @include size(33.33% auto);
  @include media-breakpoint-down(sm) {
    display: block;
    width: 100%;
    position: static; } }

.header__site-name {
  @include text-block(.875rem 1.4, $muted-text-tint flex 400);
  align-items: flex-end;
  height: 100%;
  @include media-breakpoint-down(sm) {
    display: none; } }

.header__site-name-link {
  color: $muted-text-tint;
  @include hover-link; }



