.home-works {
  @include position(relative);
  @include media-breakpoint-down(sm) {
    margin-bottom: 2rem; } }

.home-works__title {
  @include text-block(1.5rem 1.4 1rem, 700);
  font-family: Roboto, sans-serif;
  @include media-breakpoint-down(sm) {
    @include text-block(1.25rem 1.4 1rem);
    background-color: #adb7b7;
    padding: .25rem .625rem;
    color: $reversal-text;
    border-radius: .1875rem; } }

.home-works__items {
  margin-bottom: -2rem;
  @include media-breakpoint-down(sm) {
    margin-bottom: -1rem; } }

.home-works__item {
  @include size(100% auto);
  margin-bottom: 2rem;
  @include position(relative);
  cursor: pointer;
  @include media-breakpoint-down(sm) {
    margin-bottom: 1rem; } }

.home-works__item-title {
  width: 100%;
  @include text-block(.8125rem 1.4, 400 $default-text flex);
  margin-top: .5rem;
  @include media-breakpoint-down(sm) {
    font-size: .75rem; } }

.home-works__item-image {
  border-radius: .1875rem; }



