.sns-buttons {
  margin-bottom: 1.5rem;
  @include media-breakpoint-down(sm) {
    margin-bottom: -36px;
    margin-left: 7.5rem; } }
.sns-buttons__item {
  width: 25%;
  @include inline-block(bottom);
  @include relative;
  box-sizing: border-box;
  text-align: center; }
$button-size: 1.75rem;
.sns-buttons__entry-link {
  @include inline-block(bottom);
  background-color: $text-color;
  @include text-block(round($button-size/2.2) $button-size, $base-color center);
  @include size($button-size);
  @include border-radius(50%);
  background-color: $text-color;
  &:hover {
    background-color: $accent-color; } }
.sns-buttons__search-link {
  @include text-block(.7rem 1rem, center $text-color);
  @include block-link; }



