.member {
  display: flex;
  align-items: center;
  justify-content: center;
  @include size(100% auto);
  @include margin(vertical, 1.25rem);
  @include media-breakpoint-down(sm) {
    justify-content: flex-start;
    @include margin(vertical, .5rem); } }

.member__inner {
  color: $muted-text-tint;
  display: flex; }

.member__name {
  font-family: 'Roboto', sans-serif;
  @include text-block(1.25rem 1 .1875rem, 700);
  @include media-breakpoint-down(sm) {
    @include text-block(1rem); } }

.member__job {
  @include text-block(.75rem 1.4 .375rem); }

.member__image {
  @include size(4rem);
  opacity: .8;
  margin-right: 1.25rem;
  border-radius: .125rem;
  @include media-breakpoint-down(sm) {
    @include size(2.75rem);
    margin-right: .75rem; } }



