.article-publish-at {
  @include position(absolute, left -1.5rem, top -2.5rem);
  font-family: 'Roboto Mono', monospace;
  @include text-block(.875rem 1, $text-color center flex nowrap 500);
  background-color: $yellow-tint;
  @include size(5rem);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  flex-direction: column-reverse;
  flex: 0 0 5rem;
  @include media-breakpoint-down(sm) {
    display: inline-flex;
    position: static;
    height: auto;
    width: auto;
    background-color: #f7eb16;
    flex-direction: row;
    flex: 0 0 100%;
    border-radius: 0;
    justify-content: flex-start;
    padding: 3px 6px;
    position: absolute;
    top: 0;
    left: 0;
    border-top-left-radius: .1875rem; } }

.article-publish-at__date {
  padding-top: .25rem;
  @include text-block(1.125rem 1 .25em, flex);
  justify-content: center;
  @include media-breakpoint-down(sm) {
    padding-top: 0;
    @include text-block(.75rem 1 0, flex); } }

.article-publish-at__date-slash {
  font-weight: 400;
  @include margin(horizontal, -.04em);
  &.is-first {
    display: none;
    @include media-breakpoint-down(sm) {
      display: block; } } }

.article-publish-at__year {
  @include text-block(.875rem 1, block);
  @include media-breakpoint-down(sm) {
    font-size: .75rem; } }



