.home-about-us {
  @include position(relative);
  margin-bottom: 2rem; }

.home-about-us__title {
  @include text-block(1.5rem 1.4 1rem, 700);
  font-family: Roboto, sans-serif;
  @include media-breakpoint-down(sm) {
    @include text-block(1.25rem 1.4 1rem);
    background-color: #adb7b7;
    padding: .25rem .625rem;
    color: $reversal-text;
    border-radius: .1875rem; } }

.home-about-us__body {
  p {
    @include text-block(.875rem 1.7);
    @include media-breakpoint-down(sm) {
      @include text-block(.75rem 1.6); } }
  a {
    @include hover-link-reversal;
    color: #ff276e; } }

.home-about-us__body-image {
  @include size(8.5rem auto);
  margin-left: 1.25rem;
  float: right;
  @include media-breakpoint-down(sm) {
    @include size(6rem auto); } }



