.article-tags {
  height: 100%; }

.article-tags__items {
  height: 100%;
  display: flex;
  align-items: center; }

.article-tags__item {
  display: block;
  margin-right: .5rem;
  @include media-breakpoint-down(sm) {
    @include inline-block(bottom);
    margin-right: .5rem; } }

.article-tags__link {
  @include inline-block(bottom);
  @include position(relative);
  @include text-block(.75rem 1.4, nowrap);
  text-decoration: none;
  color: $text-color;
  font-family: Roboto, sans-serif;
  &:hover {
    color: $accent-color; }
  @include media-breakpoint-down(sm) {
    @include text-block(.75rem 1.6); }
  &:before {
    @include fa('\f02b');
    margin-right: .375rem;
    line-height: 1.5; } }



