.home-articles__title {
  @include text-block(1.5rem 1.4 1rem, 700);
  font-family: Roboto, sans-serif;
  @include media-breakpoint-down(sm) {
    @include text-block(1.25rem 1.4 .75rem); } }

.home-article__link {
  @include block-link;
  padding: .75rem;
  background-color: $white;
  margin-bottom: .0625rem;
  color: $default-text;
  transition: background-color .3s ease-in, filter .3s ease-in;
  .home-article:first-child & {
    @include border-radius(top, .1875rem); }
  .home-article:last-child & {
    @include border-radius(bottom, .1875rem); }
  &:hover {
    background-color: $main-color;
    .home-article__author-image {
      filter: none;
      opacity: 1; } } }

.home-article__link-inner {
  @include position(relative);
  padding-left: 2.75rem; }

.home-article__title {
  @include text-block(.8125rem 1.45 .25rem, 400); }

.home-article__created-at {
  @include text-block(.75rem 1.4, block right $muted-text); }

.home-members__lead {
  @include position(absolute, left 0, bottom 0);
  @include text-block(6.25rem .7, #d7d7d7); }

.home-article__author-image {
  @include position(absolute, left 0, top 0);
  @include size(2rem auto);
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
  opacity: .8; }



