.articles-article {
  margin-bottom: .0625rem;
  &:last-child {
    margin-bottom: 0; } }

.articles-article__inner {
  @include position(relative); }

.articles-article__content {
  @include position(relative);
  background-color: $white;
  padding: 1rem;
  display: flex;
  .articles-article:first-child & {
    @include border-radius(top, .1875rem); }
  .articles-article:last-child & {
    @include border-radius(bottom, .1875rem); }
  @include media-breakpoint-down(sm) {
    padding: .5rem .75rem; } }

.articles-article__header {
  margin-bottom: 1.5rem;
  padding-left: 1.5rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  @include media-breakpoint-down(sm) {
    margin-bottom: 0;
    padding-left: .75rem; }
  &:last-child {
    margin-bottom: 0; } }

.articles-article__title {
  @include text-block(1.125rem 1.45, 700 block);
  letter-spacing: .02em;
  width: 100%;
  @include media-breakpoint-down(sm) {
    @include text-block(.8125rem 1.45, 400);
    height: 100%; } }

.articles-article__title-link {
  @include block-link;
  color: $text-color;
  @include padding(vertical, .25rem);
  &:hover {
    color: $accent-color; } }

.articles-article__tags {
  margin-bottom: -.5rem; }



