.global-nav {
  display: flex;
  justify-content: center;
  @include margin(vertical, 4rem);
  @include media-breakpoint-down(sm) {
    @include position(fixed, left 0, bottom 0, right 0, 1);
    background-color: $yellow-tint;
    height: 2.75rem;
    @include margin(vertical, 0); } }

.global-nav__items {
  display: flex;
  justify-content: center;
  @include media-breakpoint-down(sm) {
    @include size(100%); } }

.global-nav__item {
  display: block;
  @include padding(horizontal, .25rem);
  @include media-breakpoint-down(sm) {
    height: 100%;
    flex: 0 0 25%;
    @include padding(horizontal, 0);
    &:not(:last-child) {
      border-right: solid .0625rem #cec415; } } }

.global-nav__item-link {
  @include text-block(1rem 1, $muted-text-tint);
  font-family: 'Roboto', sans-serif;
  padding: .5rem .75rem;
  border-radius: .1875rem;
  @include flex-link;
  &:hover {
    color: $reversal-text;
    background-color: $accent-color; }
  &.is-active {
    color: $accent-color;
    font-weight: 800;
    cursor: default;
    @include media-breakpoint-down(sm) {
      background-color: $main-color; }
    &:hover {
      color: $accent-color;
      background-color: transparent; } }
  @include media-breakpoint-down(sm) {
    font-size: .75rem;
    font-weight: 400;
    height: 100%;
    align-items: center;
    justify-content: center;
    border-radius: 0;
    padding: 0; } }



