.articles-article-publish-at {
  font-family: 'Roboto Mono', monospace;
  @include text-block(.875rem 1, $text-color center flex nowrap 500);
  background-color: $yellow-tint;
  @include size(4.5rem);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex: 0 0 4.5rem;
  @include media-breakpoint-down(sm) {
    @include position(relative);
    @include size(3rem);
    flex: 0 0 3rem; } }

.articles-article-publish-at__date {
  padding-top: .125rem;
  @include text-block(1rem 1 .25em, flex);
  justify-content: center;
  @include media-breakpoint-down(sm) {
    @include text-block(.6875rem 1 .1875rem, flex); } }

.articles-article-publish-at__date-slash {
  font-weight: 400;
  @include margin(horizontal, -.04em); }

.articles-article-publish-at__year {
  @include text-block(.75rem 1, block);
  @include media-breakpoint-down(sm) {
    font-size: .625rem; } }



