.commissioned-development-features {
  margin-bottom: 2rem; }

.commissioned-development-features__items {
  display: flex;
  align-items: center;
  justify-content: center;
  @include media-breakpoint-down(sm) {
    display: block; } }

.commissioned-development-features-item {
  width: 13rem;
  @include media-breakpoint-down(sm) {
    width: 100%;
    margin-bottom: .5rem;
    &:last-child {
      margin-bottom: 0; } } }

$features-item-height-mobile: 3.5rem;

.commissioned-development-features-item__link {
  transition: all .4s ease-in;
  display: block;
  @include media-breakpoint-down(sm) {
    width: 16.25rem;
    @include margin(horizontal, auto);
    @include position(relative);
    display: flex;
    align-items: center;
    min-height: $features-item-height-mobile;
    padding-left: $features-item-height-mobile + .75rem; } }

.commissioned-development-features-item__image {
  @include size(6.5rem);
  border-radius: 50%;
  border: solid .125rem $default-text;
  padding: .25rem;
  margin-bottom: .75rem;
  display: block;
  @include margin(horizontal, auto);
  @include media-breakpoint-down(sm) {
    border-width: .0625rem;
    @include size($features-item-height-mobile);
    @include margin(horizontal, 0);
    margin-bottom: 0;
    @include position(absolute, left 0, top 0); }
  .commissioned-development-features-item__link:hover & {
    border-color: #00b2e6;
    opacity: .8; } }

.commissioned-development-features-item__label {
  @include text-block(.875rem 1.4, center $default-text);
  @include media-breakpoint-down(sm) {
    text-align: left;
    font-size: .875rem; }
  br {
    @include media-breakpoint-down(sm) {
      display: none; } }
  .commissioned-development-features-item__link:hover & {
    text-decoration: underline; } }



