.archives-nav {
  margin-bottom: 2.5rem; }

.archives-nav__year-item {
  display: flex;
  &:last-child {
    margin-bottom: 0; } }

.archives-nav__year-link {
  margin-right: 1.5rem;
  @include hover-link;
  @include text-block(.9rem 1.6, $text-color block);
  font-weight: 400;
  &:hover {
    color: $accent-color; }
  @include media-breakpoint-down(sm) {
    margin-bottom: .5rem; } }

.archives-nav__month-items {
  display: flex;
  @include media-breakpoint-down(sm) {
    display: block; } }

.archives-nav__month-item {
  @include inline-block(bottom);
  @include media-breakpoint-down(sm) {
    width: 8.33%; } }

.archives-nav__month-link {
  margin-right: .5rem;
  @include hover-link;
  @include text-block(.9rem 1.6, $text-color block);
  font-weight: 400;
  &:hover {
    color: $accent-color; }
  @include media-breakpoint-down(sm) {
    margin-right: 0; } }



