.hero-important-link {
  @include position(absolute, right 0, top 0);
  @include size(8rem);
  background-color: $accent-color;
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  &:hover {
    transform: scale(1.025); }
  @include media-breakpoint-down(sm) {
    @include position(absolute, top -5rem);
    @include padding(horizontal, 1rem);
    flex-direction: row;
    @include size(auto 2.5rem);
    background-color: rgba($accent-color, .8);
    border-radius: 1.25rem;
    align-items: center; } }

.hero-important-link__label {
  @include text-block(.8125rem 1.6 .5rem, $reversal-text center);
  @include media-breakpoint-down(sm) {
    @include text-block(.75rem 1 0);
    margin-right: .5rem;
    br {
      display: none; } } }

.hero-important-link__image {
  display: block;
  @include size(2.5rem auto);
  @include media-breakpoint-down(sm) {
    @include size(1.75rem auto); } }



