.landing-page-nav {
  display: flex;
  justify-content: flex-end;
  &.is-top {
    @include margin(vertical, 5rem 1rem);
    @include media-breakpoint-down(sm) {
      margin-top: -2rem;
      @include position(relative, 1); } }
  &.is-bottom {
    @include margin(vertical, 1rem 0); } }

.landing-page-nav__link {
  @include text-block(.875rem 1, $accent-color flex);
  align-items: center;
  justify-content: center;
  border: solid .125rem $accent-color;
  height: 2.25rem;
  @include padding(horizontal, 1.25rem 1rem);
  border-radius: .1875rem;
  &:hover {
    background-color: rgba($accent-color, .2); } }

.landing-page-nav__link-icon {
  margin-left: .625rem; }



