.company-profile {
  background-color: $main-color;
  padding: .75rem 1rem;
  @include text-block(.75rem 1.5);
  @include position(relative);
  overflow: hidden;
  border-radius: .1875rem;
  @include media-breakpoint-down(sm) {
    margin-bottom: 1rem;
    padding: .625rem .75rem;
    background-color: $shade-white; } }

.company-profile__item {
  @include margin(vertical, .5rem);
  @include media-breakpoint-down(sm) {
    @include margin(vertical, .25rem); } }

.company-profile__label {
  @include inline-block(top);
  width: 3.5rem;
  text-align: right;
  @include media-breakpoint-down(sm) {
    width: 3rem; } }

.company-profile__value {
  @include inline-block(top);
  padding-left: 1.25rem;
  @include media-breakpoint-down(sm) {
    padding-left: 1rem; } }

.company-profile__to-access-link {
  @include position(absolute, right 1.25rem, bottom 1rem);
  @include block-link;
  background-color: $muted-text-tint;
  @include size(3.5rem);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  @include text-block(.625rem, $main-color);
  transition: color .2s ease-in, background-color .2s ease-in;
  @include media-breakpoint-down(sm) {
    @include position(right .75rem, right .75rem); }
  &:hover {
    color: $reversal-text;
    background-color: $accent-color; } }

.company-profile__to-access-link-icon {
  @include text-block(1.5rem 1, center); }



