.previous-next-nav {
  margin-top: 4rem;
  @include media-breakpoint-down(sm) {
    margin-top: 2rem; } }

.previous-next-nav__items {
  display: flex;
  justify-content: space-between; }

.previous-next-nav__item {
  max-width: 40%;
  &.is-previous {
    text-align: left; }
  &.is-next {
    text-align: right; } }

.previous-next-nav__link {
  @include block-link;
  @include text-block(.75rem 1.5, $text-color nowrap);
  @include position(relative);
  @include hover-link;
  overflow: hidden;
  text-overflow: ellipsis;
  .previous-next-nav__item.is-previous & {
    padding-left: 1rem;
    @include media-breakpoint-down(sm) {
      padding-left: 0; } }
  .previous-next-nav__item.is-next & {
    padding-right: 1rem;
    @include media-breakpoint-down(sm) {
      padding-right: 0; } }
  &:hover {
    color: $accent-color; }
  @include media-breakpoint-down(sm) {
    border-radius: .1875rem;
    background-color: $text-color;
    @include text-block(1rem 1, $base-color center flex);
    align-items: center;
    justify-content: center;
    @include size(2.5rem);
    &:hover {
      text-decoration: none; } } }

.previous-next-nav__icon {
  font-size: .875rem;
  top: .1875rem;
  &.is-previous {
    @include position(absolute, left 0);
    @include media-breakpoint-down(sm) {
      position: static; } }
  &.is-next {
    @include position(absolute, right 0);
    @include media-breakpoint-down(sm) {
      position: static; } } }

.previous-next-nav__label {
  display: none;
  @include text-block(.75rem 1, center);
  width: 2rem;
  padding-top: .375rem;
  @include media-breakpoint-down(sm) {
    display: block;
    width: 100%;
    font-size: .625rem; } }



