.work-members__items {
  display: flex;
  margin-bottom: -.5rem;
  @include margin(horizontal, -.5rem);
  @include media-breakpoint-down(sm) {
    display: block;
    margin-bottom: 0; } }

.work-members__item {
  @include text-block(.875rem 1 .5rem, flex);
  font-family: Roboto, sans-serif;
  @include margin(horizontal, .5rem);
  &:last-child {
    @include media-breakpoint-down(sm) {
      margin-bottom: 0; } } }

.work-members__item-label {
  margin-right: .5rem;
  &::after {
    content: ":"; } }



