.articles-article-tags {
  @include media-breakpoint-down(sm) {
    display: none; } }

.articles-article-tags__item {
  margin-bottom: -.75rem; }

.articles-article-tags__item {
  display: inline-block;
  margin-right: .5rem;
  margin-bottom: .75rem; }

.articles-article-tags__link {
  @include inline-block(bottom);
  @include position(relative);
  @include text-block(.75rem 1.6, nowrap);
  text-decoration: none;
  color: $text-color;
  &:hover {
    color: $accent-color; }
  &:before {
    @include fa('\f02b');
    margin-right: .375rem;
    line-height: 1.5; } }



