.work-data {
  border: solid .0625rem $base-color;
  margin-top: 2rem;
  padding: 1rem;
  @include media-breakpoint-down(sm) {
    padding: .75rem; } }

.middlewares {
  margin-top: .75rem; }

.middlewares__items {
  display: flex;
  flex-wrap: wrap;
  @include margin(horizontal, -.25rem);
  margin-bottom: -.5rem; }

.middlewares__item {
  display: flex;
  align-items: center;
  @include margin(horizontal, .25rem);
  background-color: red;
  padding: .1875rem .5rem;
  border-radius: .1875rem;
  color: $reversal-text;
  margin-bottom: .5rem;
  &.is-ruby {
    background-color: #e07575; }
  &.is-rubyonrails {
    background-color: #e07575; }
  &.is-heroku {
    background-color: #c3aade; }
  &.is-sass {
    background-color: #da95b7; }
  &.is-coffeescript {
    background-color: #909bb3; }
  &.is-slim {
    background-color: #e6ad6f; }
  &.is-sakuracloud {
    background-color: #a1ca8f; }
  &.is-haml {
    background-color: #e07575; }
  &.is-sinatra {
    background-color: #cab88c; }
  &.is-wordpress {
    background-color: #00749a; }
  &.is-php {
    background-color: #8892BF; }
  &.is-hugo {
    background-color: #fd91ba; }
  &.is-go {
    background-color: #7e9cd6; }
  &.is-yarn {
    background-color: #7cbad6; } }

.middlewares__item-image {
  @include size(auto .875rem);
  display: block;
  margin-right: .25rem;
  @include media-breakpoint-down(sm) {
    display: none; } }

.middlewares__item-label {
  @include text-block(.75rem 1, block);
  font-family: Roboto, sans-serif; }



