.hero {
  display: flex;
  justify-content: center;
  align-items: center;
  @include media-breakpoint-down(sm) {
    display: block;
    height: auto; }
  .container {
    width: 100%; } }

.hero__inner {
  @include position(relative);
  width: 100%; }

.hero__site-title {
  display: flex;
  justify-content: center;
  @include margin(vertical, 4rem);
  @include media-breakpoint-down(sm) {
    @include margin(vertical, 1rem 2rem); } }

.hero__site-title-link {
  display: flex;
  justify-content: center;
  &:hover {
    opacity: .8; }
  @include media-breakpoint-down(sm) {
    width: 58%;
    display: block;
    @include margin(horizontal, auto); } }

.hero__site-title-link-image {
  flex: 0 0 18.5rem;
  display: block;
  @include media-breakpoint-down(sm) {
    margin-bottom: 0;
    @include size(100% auto); } }



