.home-about {
  margin-bottom: 2rem;
  background-color: $white;
  @include media-breakpoint-down(sm) {
    background-color: transparent;
    margin-bottom: 2rem; } }

.home-about__inner {
  @include padding(vertical, 1.75rem);
  @include media-breakpoint-down(sm) {
    @include padding(vertical, 0); } }



