.sns-links {
  text-align: right;
  @include media-breakpoint-down(sm) {
    text-align: center; } }

.sns-links__item {
  @include inline-block(bottom);
  margin-right: .5rem;
  @include relative;
  @include media-breakpoint-down(sm) {
    margin-right: 4px; }
  &:last-child {
    margin-right: 0;
    @include media-breakpoint-down(sm) {
      margin-right: 4px; } } }

.sns-links__link {
  display: block;
  @include text-block(1rem 1.75rem, $reversal-text center);
  @include size(1.75rem);
  @include border-radius(50%);
  background-color: $text-color;
  transition: color .2s ease-in, background-color .2s ease-in;
  &:hover {
    background-color: $accent-color;
    color: $reversal-text; } }



