.is-long-text {
  .amazon {
    margin-top: 4rem;
    @include media-breakpoint-down(sm) {
      margin-top: 3rem; } }

  .amazon__image {
    margin-bottom: 1rem !important;
    max-width: 10rem !important;
    @include media-breakpoint-down(sm) {
      max-width: 8rem !important; } }

  .amazon__name {
    @include text-block(.75rem 1.45, center); }

  .amazon__author {
    @include text-block(.75rem 1.45, center); }

  .amazon__publisher {
    @include text-block(.75rem 1.45, center); } }



