.footer {
  background: $white;
  margin-top: 3rem;
  @include media-breakpoint-down(sm) {
    margin-top: 1.5rem; } }

.footer__inner {
  @include padding(vertical, 2rem);
  @include position(relative);
  @include media-breakpoint-down(sm) {
    padding-top: 1.5rem;
    margin-bottom: 2rem; } }

.footer__about-author {
  @include text-block(.875rem 1, right flex);
  height: 100%;
  align-items: flex-end;
  @include media-breakpoint-down(sm) {
    padding-top: 1.75rem;
    text-align: center;
    &:first-child {
      padding-top: 0; } } }

.footer__about-author-inner {
  width: 100%; }

.footer__copyright {
  display: block;
  margin-bottom: .75rem;
  font-size: .875rem;
  font-family: Roboto, sans-serif;
  @include media-breakpoint-down(sm) {
    margin-bottom: 1.25rem;
    font-size: .75rem; } }

.footer__copyright-copy {
  @include margin(horizontal, .25em); }

.footer__copyright-year {}

.footer__copyright-author {
  @include margin(horizontal, 4px); }

.footer__copyright-reserved {}



