@mixin fa($icon: null) {
  display: inline-block;
  font-family: FontAwesome;
  font-style: normal;
  font-weight: normal;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: "#{$icon}"; }

@mixin fa-icon($icon: '\f105', $size: 16px 12px, $options: null, $position1:null, $position2: null) {
  &:before {
    @include fa($icon);
    @if $position1 {
      @include position(absolute, if($position1, $position1, null), if($position2, $position2, null)); }
    @if $size {
      @include text-block($size, $options); } } }



